<template>
  <div id="app">
    <h1>The super quiz</h1>
    <transition mode="out-in" name="turn">
      <div v-if="!winState" key="ingame">
        <div class="card">
          <div class="heading">
            What is
            <strong>{{x}} {{ compY }}</strong> ?
          </div>
          <!-- <div class="heading">{{x+y}}</div> -->
          <div class="ans-group">
            <div v-for="ans in answers" :key="ans" class="ans">
              <span class="btn" @click="check(ans)">{{ans}}</span>
            </div>
          </div>
        </div>
      </div>
      <div v-else key="won">
        <div class="card">
          <h2>That is right!</h2>
          <button @click="reset()" class="btn">Another Question?</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      // sign: "+",
      x: 0,
      y: 0,
      answers: [],
      winState: false
    };
  },

  computed: {
    compY() {
      return this.y < 0 ? " - " + (0 - this.y) : " + " + this.y;
    }
  },

  methods: {
    reset() {
      // reset state
      this.winState = false;

      // reset answers
      this.answers = [];

      // random x and y
      this.x = Math.floor(Math.random() * 50);
      this.y = Math.floor(Math.random() * 50);
      let sign = Math.random();
      // Math.random()
      if (sign < 0.5) {
        this.y = 0 - this.y;
      }

      // fill answers
      let ans;
      while (this.answers.length != 3) {
        ans = this.x + this.y + Math.floor(Math.random() * 20 - 10);
        if (
          ans !== this.x + this.y &&
          ans !== this.answers[0] &&
          ans !== this.answers[1]
        ) {
          this.answers.push(ans);
        }
      }

      // add true answer at random position
      ans = this.x + this.y;
      let index = Math.floor(Math.random() * this.answers.length - 0.49);
      this.answers.splice(index, 0, ans);
    },

    check(ans) {
      if (ans !== this.x + this.y) {
        alert("false, try again");
      } else {
        this.winState = true;
      }
    }
  },

  created() {
    this.reset();
  }
};
</script>

